.contents ul li a {
  text-decoration: none;
  color: var(--black);
}
.htmlTags {
  display: block;
  margin: 100px auto;
}
.htmlTags table {
  margin: auto;
  width: 70%;
  border: 1px solid var(--theme);
}
.htmlTags table,
td,
th,
caption {
  border-collapse: collapse;
  font-size: 1rem;
  text-align: center;
  height: 50px;
}
.htmlTags tr:nth-child(even) {
  background-color: var(--striped);
  border: 1px solid grey;
}
td:first-of-type {
  color: var(--black);
  font-weight: bold;
}
.htmlTags th {
  font-size: 1rem;
  color: var(--white);
  font-weight: bold;
  background-color: var(--theme);
}
.htmlTags caption {
  border-bottom: 0;
  font-size: 1.2rem;
}
@media (max-width: 992px) {
  #heading {
    font-size: 20px;
  }
  .contents ul li {
    font-size: 16px;
  }
  .htmlTags table,
  td,
  th,
  caption {
    font-size: 15px;
  }
  .arrow-up i {
    left: 95vw;
    font-size: 45px;
  }
}
@media (max-width: 768px) {
  .section-hero-heading {
    text-align: center;
    font-size: 1.8rem;
  }
  #heading {
    font-size: 16px;
  }
  .htmlTags table,
  td,
  th,
  caption {
    font-size: 13px;
  }
  .arrow-up i {
    left: 90vw;
    font-size: 40px;
  }
  .html-info {
    max-width: 90%;
  }
}
@media (max-width: 576px) {
  .section-hero-heading {
    text-align: center;
    font-size: 2rem;
  }
  .section-hero-heading::after{
    visibility: hidden;
  }
  .htmlTags table {
    width: 90%;
  }
  #heading {
    font-size: 14px;
  }
  .contents ul {
    width: 300px;
  }
  .contents ul li {
    font-size: 16px;
  }
  .arrow-up i {
    left: 90vw;
  }
}

.about-me-hero {
    width: 100%;
    height: 70vh;
    background-image: url(/basic-portfolio-site/static/media/about-me-hero.00df0334.jpg);
    background-size: cover;
    min-height: 700px;
    background-position: top top;
    overflow: hidden;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.about-me-hero:before {
    content: '';
    background-color: #00000080;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.about-me-hero:after {
    content: '';
    border: 2px solid white;
    border-top-color: var(--white-muted);
    border-right-color: var(--white-muted);
    -webkit-transform: rotateZ(135deg);
            transform: rotateZ(135deg);
    padding: 5px;
    border-left-color: transparent;
    border-bottom-color: transparent;
    position: absolute;
    left: 50%;
    top: 80vh;
}
.row {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 0.8fr));
    grid-template-rows: 500px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    grid-auto-flow: dense;
    grid-gap: 20px;
}
.main-content {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    line-height: 1.5rem;
    color: var(--black-muted);
    padding-right: 20px;
}
.main-content-heading {
    font-size: 2rem;
    color: var(--theme);
    margin: 20px 0px;
    padding: 20px 0px;
    position: relative;
}
.main-content-heading:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: var(--theme);
    height: 3px;
    width: 30%;
}
.main-content-button {
    background-color: var(--theme);
    padding: 1rem;
    font-size: 1rem;
    color: var(--white);
    border-radius: 3px;
    margin: 50px;
    border: 0;
    z-index: 10;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.main-content-article {
    padding: 10px 0px;
    line-height: 1.8rem;
}
.main-content-article::first-letter {
    font-size: 1.5rem;
}
.main-content-button:hover {
    background-color: var(--theme-hover);
    transition: all 0.3s ease-in-out;
}
.heading {
    color: var(--theme);
}
.profile_image {
    display: grid;
    place-items: center;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin: auto;
    position: relative;
}
.profile_image:before {
    content: '';
    position: absolute;
    border-radius: 50%;
    top: -2%;
    left: -2%;
    bottom: -2%;
    right: -2%;
    z-index: -1;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #1eb0e6, #1ce2b4);
    background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
            animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.profile_image img {
    border-radius: 50%;
    width: inherit;
}
@media (max-width: 768px) {
    .main-content-article {
        padding-bottom: 80px;
        line-height: 1.8rem;
    }
    .row {
        grid-template-columns: repeat(auto-fit, minmax(320px, 0.8fr));
    }
}

